import {
	baseUrl
} from './config.js';

// 
export function login(params) {
	return uni.request({
		url: `${baseUrl}/login/cellphone`,
		method: 'POST',
		data: params
	})
}
// 
export function register(params) {
	return uni.request({
		url: `${baseUrl}/register/cellphone`,
		method: 'POST',
		data: params
	})
}
// 
export function sendCode(params) {
	return uni.request({
		url: `${baseUrl}/captcha/sent`,
		method: 'POST',
		data: params
	})
}
// 
export function sendCodeVerify(params) {
	return uni.request({
		url: `${baseUrl}/captcha/verify`,
		method: 'POST',
		data: params
	})
}
// 
export function topList() {
	// idx 
	// var listIds = ['3' , '0' , '2' , '1' ];
	// id
	var listIds = ['19723756', '3779629', '2884035', '3778678'];
	return new Promise(function(resolve, reject) {
		uni.request({
			// 
			url: `${baseUrl}/toplist/detail`,
			// 
			method: 'GET',
			data: {},
			// 
			success: res => {
				// 
				let result = res.data.list;
				result.length = 4;
				// 
				for (var i = 0; i < result.length; i++) {
					result[i].listId = listIds[i];
				}
				resolve(result);
			},
			fail: (err) => {
				console.log(err);
			},
			complete: () => {}
		});
	});
}
// 
export function list(listId) {
	return uni.request({
		// idx
		// url: `${baseUrl}/top/list?idx=${listId}`,
		// 
		url: `${baseUrl}/playlist/detail?id=${listId}`,
		method: 'GET'
	});
}
// 
export function songDetail(id) {
	return uni.request({
		///song/detail?ids=347230
		///playlist/detail?id=
		url: `${baseUrl}/song/detail?ids=${id}`,
		method: 'GET'
	})
}
